import bridge from '@vkontakte/vk-bridge';
import React from 'react';
import ReactDOM from 'react-dom';
import { AppRoot, View, Panel, PanelHeader, Header, Group, Cell, Button, Input, Div, FixedLayout, PromoBannerProps } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
const axios = require('axios');

bridge.send("VKWebAppInit", {});

var user;
bridge.send('VKWebAppGetUserInfo').then(res => {user = res;}).then(() => {work()});

const changeMass = () => {
    document.getElementById('mass').value = String(Number(document.getElementById('mass').value)+1)
}

class App extends React.Component {
    render () {
        return (
            <AppRoot>
                <View style={{ backgroundColor: "#ffffff" }} activePanel="main">
                    <Panel id="main">
                        <PanelHeader>Ваша жаба</PanelHeader>
                        <Header mode="secondary">{user.first_name}, ваша жаба скучала за вами!</Header>
                        <Div><h3>Масса: </h3><h2 id="mass">0</h2></Div>
                        <img width="50%" onClick={() => changeMass()} src="https://emojitool.ru/img/apple/ios-13.3/frog-face-2959.png" style={{
                            textAlign: "center", display: "block", justifyContent: "center", alignItems: "center", marginLeft: "auto", marginRight: "auto",
                            marginTop: "5%", marginBottom: "5%"
                        }} />
                        <Div>
                            <Button size='l' mode="outline" style={{ width: '48.5%' }}>Топ жаб</Button>
                            <Button size='l' mode="outline" style={{ marginLeft: '3%', width: '48.5%' }}>Друзья</Button>
                            <Button size='l' mode="outline" style={{ marginTop: '3%', width: '100%' }}>Реферальная система</Button>
                        </Div>
                        <FixedLayout vertical="bottom">
                            тут будет реклама
                        </FixedLayout>
                    </Panel>
                </View>
            </AppRoot>
        );
    }
}

const work = () => {ReactDOM.render(<App />, document.getElementById('render_all'));}